import Vue from 'vue'
import wx1 from './wx1'
import wx2 from './wx2'
import wx3 from './wx3'
import wx4 from './wx4'
import Router from 'vue-router'
Vue.use(Router)

var routes=[
	{
		path:'/',
		name:'wx1',
		component:wx1
	},
	{
		path:'/wx2',
		name:'wx2',
		component:wx2
	},
	{
		path:'/wx3',
		name:'wx3',
		component:wx3
	},
	{
		path:'/wx4',
		name:'wx4',
		component:wx4
	}
]

export default new Router({
	routes
})


