 	font-size: 1.125rem;
 	 color: #CCCCCC;
 	 float: left;
	 margin-left: 0.1875rem;
 }
 .wx{
	 font-size: 0.55rem;
	 color: #000000;
	 float: left;
	 line-height: 1.25rem;
	 margin-top: 0.3125rem;
	 }
 .mint-tab-item-label{
	 line-height: 0;
	 color: #000000;
	  font-size: 0;
	  margin-left: 30%;
	  margin-top:0.75rem;
 }
</style>



